@import url('https://fonts.googleapis.com/css2?family=Honk&family=Nothing+You+Could+Do&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');

.projects-left{
    height: 100vh;
    background-color: #0A0A0A;
}

.projects-left h1{
    font-family: "Nothing You Could Do", cursive;
}

.projects-left p{
    font-family: "Quicksand", sans-serif;
}

.projects-right{
    font-family: "Quicksand", sans-serif;
}

.projects-gallery{
    overflow-y: auto;
    height: 85vh;
}

.projects-gallery::-webkit-scrollbar {
    width: 0px;
    height: 12px;
}

.projects-gallery::-webkit-scrollbar-thumb {
    background: #0A0A0A;
    border-radius: 6px;
}

.projects-gallery::-webkit-scrollbar-thumb:hover {
    background: #0A0A0A;
}

.projects-gallery::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 6px;
}